<template>
  <b-row class="m-0 p-0">
    <b-col class="p-0" cols="auto">
      <b-button class="brd-se bg-pr" @click="isMute = !isMute">
        <b-icon :icon="isMute?'volume-off':'volume-up'" class="text-pr" font-scale="2"/>
      </b-button>
    </b-col>
    <b-col class="p-0 pl-3" fluid @mousewheel="whell">
      <b-input type="range" v-model="volume" :max="maxSteps"/>
    </b-col>
  </b-row>
</template>

<script>
import DataShare from '../helpers/dataShare'

function inerval(val, min, max){
  return Math.min(Math.max(val, min), max)
}

const step = 1;
const maxSteps = 10;

export default {
  // components: {},
  // filters: {},
  // props: [],
  data() {
    return {
      maxSteps,
      volume: window.$data.aud.vol * maxSteps,
      isMute: window.$data.aud.isMute,
    };
  },
  // computed: {},
  watch: {
    volume(v){
      this.volume = inerval(v, 0, maxSteps);
      this.setData();
    },
    isMute(){ this.setData() },
  },
  created(){
    this.setData();
  },
  methods: {
    whell(e){
      const delY = e.deltaY
      this.volume -= delY>0 ? step : -step;
    },
    setData(){
      DataShare.setAudStgs({
        vol: this.volume/maxSteps,
        isMute: this.isMute,
      });
    },
  },
}
</script>

