<template>
  <b-row v-if="simp" cols="3">
    <b-col v-for="(projSimpleData, ix) in projects" :key="ix">
      <b-card 
        class="bg-transparent brd-se"
        body-class="p-2"
      >
        <!-- :class="sel === ix ? 'brd-ac' : 'brd-se'" -->
        <!-- @dblclick="sel = ix; $emit('select', ix)" -->
        <img-pixel :src="projSimpleData.p"/>
        <h3 class="text-nowrap overflow-hidden m-2 ml-4 text-pr">{{ projSimpleData.n }}</h3>
        <h5 class="overflow-hidden m-2 ml-4 text-ma">{{ projSimpleData.d }}</h5>
        <b-button class="float-right m-1 bg-lt brd-ac" @click="$emit('play', ix)">
          <b-icon-play/> Играть
        </b-button>
      </b-card>
    </b-col>
  </b-row>
  <b-row v-else cols="3">
    <b-col v-for="(projPair, ix) in projects.entries()" :key="ix">
      <b-card 
        class="bg-transparent brd-se"
