<template>
  <div id="game" class="h-100 w-100">
    <b-container v-if="!isPlayingMode" fluid class="w-100 h-100">
      <b-row class="m-2">
        <b-button class="w-25 bg-pr" :class="{'brd-ac':!isLocalProjs}" @click="isLocalProjs = false">Публичные</b-button>
        <b-button class="w-25 bg-pr ml-2" :class="{'brd-ac':isLocalProjs}" @click="isLocalProjs = true">Локальные</b-button>
      </b-row>
      <GamesList v-if="!isLocalProjs" simple :projects="publicProjects" @play="startGame"/>
      <GamesList v-else :projects="localProjs" @play="startGame"/>
      <!-- <b-row class="p-2">

        <b-form-select size="lg" v-model="sLocalProjKey" class="mb-3">
          <b-form-select-option v-for="key in localProjs" :key="key" :value="key">{{key}}</b-form-select-option>
        </b-form-select>
        <b-button size="lg" @click="startGame">Start</b-button>
      </b-row> -->
      <!-- <b-nav>
        <b-nav-item active>Active</b-nav-item>
        <b-nav-item>Link</b-nav-item>
        <b-nav-item>Another Link</b-nav-item>
        <b-nav-item disabled>Disabled</b-nav-item>
      </b-nav> -->
      <!-- <script defer src="<%= BASE_URL %>game/game.js"></script> -->

      <!-- <b-button size="lg" class="ml-2"  :variant="isDebug ? 'primary' : ''" @click="isDebug = !isDebug">Debug</b-button> -->
    </b-container>
    <div class="loadinfo p-4 bg-pr brd-ac text-pr" style="border: 2px solid" v-show="progress[0]!=null && progress[0] !== progress[1]">
      <ProgressBar :val="progress"/>
      {{progress[2]}}
    </div>
    <div v-show="isPlayingMode" id="game_screen" class="h-100 w-100"></div>

    <b-modal
      id="modal_menu"
      style="bodrer: 0"
      body-class="bg-dr text-pr p-0"
      centered
      hide-header
      hide-footer
      no-close-on-backdrop
      no-close-on-esc
    >
      <b-container class="p-0 ff-main">
        <b-row class="m-0 p-2 t-b bg-pr">
          <div class="text-center w-100">Меню</div>
        </b-row>
        <b-row class="w-75 mx-auto mt-4">
          <b-button class="brd-se w-100 bg-lt" @click="toggleMenuState(false)">Продолжить</b-button>
        </b-row>
        <b-row class="w-75 mx-auto mt-2">
          <b-button class="brd-se w-100 bg-pr" @click="restart">Перезапустить</b-button>
        </b-row>
        <b-row class="w-75 mx-auto mt-2">
          <AudioSettings class="w-100" :ismin="false"/>
        </b-row>
        <b-row class="w-75 mx-auto mt-2 mb-4">
          <b-button class="brd-se w-100 bg-pr" @click="endGame">Завершить игру</b-button>
        </b-row>
        <b-row v-if="isRequireToRunLocal" class="w-75 mx-auto mt-2 mb-4">
          <b-button class="brd-se w-100 bg-pr" @click="window.close()">Закрыть вкладку</b-button>
        </b-row>
      </b-container>
      <!-- <p style="height: 30vh">
        {{modal.msg}}
      </p>
      <slot slot="modal-footer">
        <b-button v-for="(btn, i) in modal.btns" :key="i" :variant="btn[1]" @click="modal.onanswer(i); $bvModal.hide('modal_dialog')">
          {{btn[0]}}
        </b-button>
      </slot>   -->
    </b-modal>

    <b-modal
      id="modal_finish_menu"
      style="bodrer: 0"
      body-class="bg-dr text-pr p-0"
      centered
      hide-header
      hide-footer
      no-close-on-backdrop
      no-close-on-esc
    >
      <b-container class="p-0 ff-main">
        <b-row class="m-0 p-2 t-b bg-pr">
          <div class="text-center w-100">{{isFinishGameVictory ? 'Победа' : 'Поражение'}}</div>
        </b-row>
        <b-row class="w-75 mx-auto mt-4">
          <b-button class="brd-se w-100 bg-pr" @click="restart">Начать сначала</b-button>
        </b-row>
        <b-row class="w-75 mx-auto mt-2 mb-4">
          <b-button class="brd-se w-100 bg-pr" @click="endGame">Завершить игру</b-button>
        </b-row>
        <b-row v-if="isRequireToRunLocal" class="w-75 mx-auto mt-2 mb-4">
          <b-button class="brd-se w-100 bg-pr" @click="window.close()">Закрыть вкладку</b-button>
        </b-row>
      </b-container>
    </b-modal>
    
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

import LS from '../helpers/localStorage';
import Convert from '../helpers/convert';

const LSK_projPef = 'p' // Local storage key: project prefix
import DataShare from '../helpers/dataShare';
import ProgressBar from '../components/base/ProgressBar';
import AudioSettings from '../components/AudioSettings';
import GamesList from '../components/game/GamesList.vue';

import publicProjects from '../../public/e/pubprojs.json';

window.addEventListener('keydown', e=>{
  if(e.key === 'Escape') vuec.tryTogglePauseMenu();
})
window.addEventListener('blur', ()=>{
  vuec.tryTogglePauseMenu(true);
})

// NOTE: Demo code
  // import ProjectEditor from '../helpers/projectEditor';
  // import JSONSchemaHelper from '../helpers/JSONSchema';
  // import schema_project from '../../public/schemas/project.json';
  // import DataShare from '../helpers/dataShare';
// NOTE: Demo code
let vuec;
const LSK_isLocalProjs = 'g_ilp'

export default {
  data(){
    return {
      window,
      publicProjects,
      isRequireToRunLocal: this.$route.query.runloc != null,
      isLocalProjs: LS.load(LSK_isLocalProjs, false, Convert.from.bool),
      isPlayingMode: false,
      sProjData: {},
      localProjs: LS.many.loadAll(LSK_projPef),
      sLocalProjKey: '',
      isDebug: false,
      isMenuShowed: false,
      isFinishMenuShowed: false,
      isFinishGameVictory: false,
    }
  },
  components: {
    ProgressBar,
    AudioSettings,
    GamesList,
  },
  computed: {
    ...mapGetters(['progress']),
  },
  mounted(){
    vuec = this;
    if(this.isRequireToRunLocal){
      this.isLocalProjs = true;
      this.startGame(this.$route.query.runloc);
    }else{
      this.sLocalProjKey = this.localProjs[0];
    }
    // const projData = LS.load(this.sLocalProjKey, {}, Convert.from.json);
    // this.startGame(isLocal, projData);
    // setTimeout(this.startGame, 500, isLocal, projData);
    // this.startGame()
    // Storage.watch('stg', function(newval) {
    //   alert(newval);
    // });
    // Storage.save('stg', '12rfgwe')

    
    // Screen.methods.startEngine();
    // console.log(':');
    // eslint-disable-next-line no-console
    // console.dir(this);
    // this.$state.dispatch('test');
    // this.$router.push('/'); // пока так
  },
  destroyed(){
    // alert('компонент уничтоден')
    this.endGame();
  },
  watch: {
    isLocalProjs(v){
      LS.save(LSK_isLocalProjs, v, Convert.to.bool);
    },
    // sLocalProjKey(v){
    //   this.sProjData = LS.load(v, {}, Convert.from.json);
    // },
  },
  methods:{
    loadProgress(curStep, totalSteps, message){
      this.$store.commit('set', { progress: [curStep, totalSteps, message] })
    },
    tryTogglePauseMenu(isShow){
      if(!this.isPlayingMode)return;
      this.toggleMenuState(isShow);
    },
    toggleMenuState(isShowed = !this.isMenuShowed){
      if(this.isFinishMenuShowed)return;
      this.isMenuShowed = isShowed;
      this.$bvModal[isShowed?'show':'hide']('modal_menu');
      window.$triggers.setPause(isShowed);
    },
    toggleFinishMenuState(isShowed = !this.isMenuShowed, isVictory = false){
      this.isFinishMenuShowed = isShowed;
      this.$bvModal[isShowed?'show':'hide']('modal_finish_menu');
      this.isFinishGameVictory = isVictory;
      // window.$triggers.setPause(isShowed);
    },
    restart(){
      this.toggleMenuState(false);
      window.$triggers.destroy();
      this.startGame();
    },
    async startGame(key = this.sLocalProjKey){
      this.$store.dispatch('setSidebarHide')

      let project;
      this.sLocalProjKey = key;
      if(this.isLocalProjs){
        project = Convert.from.json(this.localProjs.get(key));
      }else{
        project = await import(`../../public/e/project/${key}.json`).then(d=>d.default);
      }

      this.loadProgress(0, 1, 'Загрузка проекта')
      this.isPlayingMode = true;

      const startData = {
        project,
        parent: 'game_screen',
        isLocal: this.isLocalProjs,
        mapId: 0,
        loadProgressCB: this.loadProgress,
        finishCB(isVictory){ vuec.toggleFinishMenuState(true, isVictory)},
      };
      // console.log('startData')
      // console.dir(startData)
      const engType = 'game';

      DataShare.clearTriggersAndEvents();
      window.$events.onEngineLoad = init;
      
      await new Promise(s=>setTimeout(s, 500));

      this.$store.dispatch('loadEngine', engType)
        // .then(()=>{})
        .catch(console.error)

      function init() {
        window.$triggers.start(startData)
      }
    },
    endGame(isClearRequest = true){
      this.loadProgress(0, 0); // Сокрытие прогрессбара загрузки

      if(isClearRequest){
        this.isRequireToRunLocal = false;
        this.$router.replace({
          query: { runloc: undefined }
        })
      }

      this.$store.dispatch('setSidebarHide', false);
      window.$triggers.destroy();
      this.toggleMenuState(false);
      this.toggleFinishMenuState(false);
      this.isPlayingMode = false;
    }
  },
}
</script>

<style>
  #game_screen{
    position: fixed;
  }
  #game_screen>*{
    margin:0 !important;
  }
</style>
